import { Link } from "react-router-dom";
import style from './country.module.css';

export default function Country({ id, name, flag, continent }) {
    return <div className={style.card}>
        <Link
            to={`/id/${id}`}
            className={style.sinSubrayado}
        >
            <h1 className={style.h1}>{name}</h1>
            <h3 className={style.h3}>{continent}</h3>
            <img
                src={flag}
                alt={name + 'flag'}
                className={style.img}
            />
            <h3 className={style.h3}>{id}</h3>
        </Link>
    </div>
};

/*
Este c√≥digo es una funci√≥n de componente React llamada "Country", 
que devuelve un fragmento de JSX (JavaScript XML) 
que representa una tarjeta (card) de un pa√≠s. 
La funci√≥n acepta cuatro propiedades como argumentos: 
"id", "name", "flag" y "continent".

La funci√≥n "Country" utiliza la etiqueta "Link" importada desde "react-router-dom" 
para permitir la navegaci√≥n hacia una p√°gina de detalles de un pa√≠s espec√≠fico 
cuando se hace clic en la tarjeta. 
La propiedad "to" de "Link" es un string que representa la ruta a la p√°gina de detalles 
y utiliza la variable "id" pasada como propiedad. 
La propiedad "className" se utiliza para agregar una clase CSS llamada "sinSubrayado" 
a la etiqueta "Link".

El contenido de la tarjeta de pa√≠s se envuelve dentro de un div con una clase CSS 
llamada "card" importada desde el archivo "./country.module.css". 
Dentro del div, hay un encabezado h1 con el nombre del pa√≠s, 
un encabezado h3 con el continente del pa√≠s, 
una imagen con la bandera del pa√≠s 
y un encabezado h3 con el ID del pa√≠s. 
Cada elemento tiene una clase CSS espec√≠fica para darle estilo al componente.
*/